/*СТИЛИ ДЛЯ КАСТОМНОГО СЕЛЕКТА НАЧАЛО*/
.custom-select-wrapper {
  position: relative;
  display: block;
  user-select: none;
  background: #F4F5F4;
  margin-bottom: 1.38889vw; }
  .custom-select-wrapper select {
    display: none; }

.custom-select {
  position: relative;
  display: block;
  font-size: 1.11111vw;
  padding-left: 1.04167vw; }

.custom-select-trigger {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 2.98611vw;
  width: 100%;
  cursor: pointer; }
  .custom-select-trigger:after {
    position: absolute;
    display: block;
    content: '';
    width: 10px;
    height: 10px;
    top: 50%;
    right: 25px;
    margin-top: -3px;
    border-bottom: 1px solid #373F41;
    border-right: 1px solid #373F41;
    transform: rotate(45deg) translateY(-50%);
    transition: all .4s ease-in-out;
    transform-origin: 50% 0; }

.custom-select.opened .custom-select-trigger:after {
  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%); }

.custom-options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  border: 1px solid #b5b5b5;
  border-radius: 4px;
  box-sizing: border-box;
  background: #fff;
  transition: all .4s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px); }

.custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  z-index: 10;
  transform: translateY(0); }

.custom-option {
  position: relative;
  padding-left: 1.04167vw;
  border-bottom: 1px solid #b5b5b5;
  font-size: 1.11111vw;
  height: 2.98611vw;
  cursor: pointer;
  transition: all .4s ease-in-out;
  display: flex;
  align-items: center; }
  .custom-option:first-of-type {
    border-radius: 4px 4px 0 0; }
  .custom-option:last-of-type {
    border-bottom: 0;
    border-radius: 0 0 4px 4px; }

