.popupWrapper {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #e5e5e5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    .modal {
        background: $lightTextOne;
        width: get-vw(1100px);
        position: relative;
        margin: auto;
        box-shadow: 0px get-vw(4px) get-vw(15px) rgba(0, 0, 0, 0.07);
        &__title {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            height: get-vw(68px);
            filter: drop-shadow(0px 4px 15px rgba(0, 0, 0, 0.07));
            z-index: 20;
            background: $lightTextOne;
            position: relative;
            &_text {
                font-size: get-vw(26px);
                font-weight: 600;
                color: $titleText; }
            &_button {
                position: absolute;
                width: get-vw(12px);
                height: get-vw(12px);
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                right: get-vw(28px);
                cursor: pointer;
                &::after, &::before {
                    content: '';
                    display: inline-block;
                    width: get-vw(15px);
                    height: get-vw(2px);
                    background: $titleText;
                    position: absolute;
                    transform: rotate(45deg); }
                &::before {
                    transform: rotate(-45deg); } } }
        &__body {
            display: flex;
            flex-direction: row;
            &>div {
                width: 50%; }
            &_left {
                box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.2);
                filter: drop-shadow(0px 4px 15px rgba(0, 0, 0, 0.07));
                z-index: 10;
                display: flex;
                justify-content: center;
                align-items: center;
                color: $titleText;
                display: flex;
                flex-direction: column;
                .imageWrapper {
                    filter: drop-shadow(0px 4px 15px rgba(0, 0, 0, 0.07));
                    background: radial-gradient(144.33% 131.39% at 0% 0%, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0) 100%);
                    backdrop-filter: blur(42px);
                    img {
                        max-height: get-vw(403px);
                        max-width: get-vw(403px);
                        height: auto;
                        width: auto; } }
                .song__text_title {
                    font-size: get-vw(26px);
                    font-weight: 700;
                    margin-bottom: get-vw(8px);
                    margin-top: get-vw(21px); }
                .song__text_description {
                    font-size: get-vw(18px);
                    font-weight: 400; } }
            &_right {
                max-height: get-vw(624px);
                overflow: hidden;
                background: #FBFBFB;
                overflow-y: scroll;
