                .customizeProduct {
                    display: flex;
                    flex-direction: column;
                    padding: get-vw(30px);
                    input[type="text"] {
                        height: get-vw(43px);
                        font-size: get-vw(16px);
                        padding-left: get-vw(15px);
                        margin-bottom: get-vw(20px);
                        outline: none;
                        border: none;
                        background: $lightTextTwo;
                        color: $titleText;
                        &::placeholder {
                            color: #C3CBCD;
                            font-size: get-vw(16px); } }
                    .titleChoice {
                        font-size: get-vw(18px);
                        font-weight: 600;
                        color: $titleText; }
                    .checkboxWrapp {
                        margin-top: get-vw(14px);
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items: center;
                        &.standChoice {
                            margin-bottom: get-vw(20px); }
                        &__text {
                            color: $descriptionText;
                            font-size: get-vw(16px); }
                        &__choice {
                            display: flex;
                            flex-direction: row;
                            label {
                                color: #C3CBCD;
                                font-size: get-vw(15px);
                                display: flex;
                                align-items: center;
                                flex-direction: row;
                                margin-left: get-vw(17px);
                                input {
                                    display: none; }
                                .likeRadio {
                                    width: get-vw(16px);
                                    height: get-vw(16px);
                                    border: 1px solid #C3CBCD;
                                    display: inline-block;
                                    border-radius: 50%;
                                    margin-right: get-vw(5px);
                                    position: relative;
                                    &::after {
                                        content: '';
                                        display: inline-block;
                                        background: $titleText;
                                        opacity: 0;
                                        left: 50%;
                                        top: 50%;
                                        transform: translate(-50%, -50%);
                                        transition: 0.3s ease-in-out;
                                        width: get-vw(8px);
                                        height: get-vw(8px);
                                        position: absolute;
                                        border-radius: 50%; } }
                                &.active {
                                    & .likeRadio {
                                        border: 1px solid $titleText;
                                        &::after {
                                            opacity: 1; } } } } } }
                    textarea {
                        border: none;
                        outline: none;
                        background: $lightTextTwo;
                        resize: none;
                        height: get-vw(95px);
                        padding-left: get-vw(15px);
                        padding-top: get-vw(11px);
                        box-sizing: border-box;
                        margin-top: get-vw(20px);
                        font-size: get-vw(16px); }
                    .timeWrapper {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: space-between;
                        margin-top: get-vw(14px);
                        margin-bottom: get-vw(20px);
                        &__text {
                            color: $descriptionText;
                            font-size: get-vw(16px); }
                        &__time {
                            width: get-vw(82px);
                            height: get-vw(27px);
                            background: $lightTextTwo;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-size: get-vw(15px);
                            color: $titleText; } } } } } } }


.openPopup {
    background: red;
    font-size: 30px;
    position: fixed;
    bottom: 0;
    width: 100%; }
